    <div class=\"mobile-menu-overlay\" v-if=\"isMenuOpen\" @click=\"toggleMenu\"></div>\n        <div class=\"mobile-menu\" :class=\"{ 'open-menu': isMenuOpen }\">\n            <div class=\"menu-box\">\n                <button class=\"close-btn\" @click=\"toggleMenu\">&times;</button>\n                <ul class=\"nav-links\">\n                    <li><a href=\"/\" @click=\"toggleMenu\">Accueil</a></li>\n                    <li><a href=\"/portfolio\" @click=\"toggleMenu\">Portfolio</a></li>\n                    <li><a href=\"/cv\" @click=\"toggleMenu\">Mon CV (Téléchargeable)</a></li>\n                    <li><a href=\"/veille\" @click=\"toggleMenu\">Veille Technologique</a></li>\n                </ul>\n                <a href=\"/securedashboard\" class=\"dashboard-btn\" @click=\"toggleMenu\">Tableau de bord</a>\n            </div>\n        </div>\n    </header>\n</template>\n\n<script setup>\nimport { ref, onMounted, onUnmounted } from \"vue\";\n\nconst screenWidth = ref(window.innerWidth);\nconst isMenuOpen = ref(false);\n\nconst toggleMenu = () => {\n    isMenuOpen.value = !isMenuOpen.value;\n};\n\nconst updateScreenWidth = () => {\n    screenWidth.value = window.innerWidth;\n};\n\nonMounted(() => {\n    window.addEventListener(\"resize\", updateScreenWidth);\n    document.body.style.paddingTop = \"90px\";\n});\nonUnmounted(() => {\n    window.removeEventListener(\"resize\", updateScreenWidth);\n});\n</script>\n\n<style scoped>\n/* 🌐 NAVBAR DESKTOP */\n.navbar-desktop {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    background: rgba(0, 0, 0, 0.85);\n    backdrop-filter: blur(8px);\n    padding: 15px 0;\n    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);\n    z-index: 999;\n    display: flex;\n    justify-content: center;\n}\n\n.container {\n    width: 100%;\n    max-width: 1400px;\n    display: flex;\n    justify-content: center;\n}\n\n.nav-box {\n    width: 100%;\n    background: rgba(255, 255, 255, 0.1);\n    padding: 12px 30px;\n    border-radius: 15px;\n    border: 2px solid rgba(255, 255, 255, 0.3);\n    box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.2);\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n}\n\n.nav-links {\n    display: flex;\n    list-style: none;\n    gap: 50px;\n    margin: 0;\n    padding: 0;\n}\n\n.nav-links a {\n    text-decoration: none;\n    color: #ffffff;\n    font-size: 18px;\n    font-weight: 500;\n    transition: 0.3s ease;\n}\n\n.nav-links a:hover {\n    color: #ffcc00;\n    text-shadow: 0px 0px 10px rgba(255, 204, 0, 0.8);\n}\n\n.dashboard-btn {\n    background: linear-gradient(45deg, #ffcc00, #ff6600);\n    padding: 10px 18px;\n    color: white;\n    border-radius: 8px;\n    font-weight: bold;\n    transition: 0.3s ease;\n    text-decoration: none;\n    font-size: 16px;\n}\n\n.dashboard-btn:hover {\n    transform: scale(1.1);\n}\n\n/* 📱 NAVBAR MOBILE */\n.navbar-mobile {\n    position: fixed;\n    top: 0;\n    width: 100%;\n    background: rgba(0, 0, 0, 0.85);\n    padding: 15px;\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n    z-index: 999;\n}\n\n.menu-btn {\n    background: none;\n    border: none;\n    cursor: pointer;\n}\n\n.icon {\n    color: #fff;\n    font-size: 30px;\n}\n\n/* 📱 MENU MOBILE */\n.mobile-menu-overlay {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background: rgba(0, 0, 0, 0.6);\n    z-index: 998;\n    display: none;\n}\n\n.show-overlay {\n    display: block;\n}\n\n.mobile-menu {\n    position: fixed;\n    top: 15px;\n    left: -300px;\n    width: 270px;\n    height: 100%;\n    background: #222;\n    box-shadow: 4px 0px 10px rgba(0, 0, 0, 0.5);\n    transition: left 0.3s ease-in-out;\n    z-index: 999;\n    display: flex;\n    align-items: center;\n    justify-content: start;\n    flex-direction: column;\n}\n\n.open-menu {\n    left: 0;\n}\n\n.menu-box {\n    background: rgba(255, 255, 255, 0.1);\n    padding: 20px;\n    border-radius: 15px;\n    border: 2px solid rgba(255, 255, 255, 0.3);\n    box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.2);\n    width: 90%;\n    text-align: center;\n}\n\n.mobile-menu .nav-links {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 50px;\n}\n\n.mobile-menu .nav-links a {\n    color: white;\n    font-size: 18px;\n    text-decoration: none;\n    transition: 0.3s ease;\n    padding: 10px 15px;\n    border-radius: 8px;\n}\n\n.mobile-menu .nav-links a:hover {\n    background: rgba(255, 255, 255, 0.2);\n}\n\n.mobile-menu .dashboard-btn {\n    display: block;\n    margin: 20px auto;\n    padding: 12px 20px;\n    text-align: center;\n    font-size: 16px;\n    background: linear-gradient(45deg, #ffcc00, #ff6600);\n    color: white;\n    border-radius: 8px;\n    font-weight: bold;\n    transition: background 0.3s ease;\n    width: 80%;\n}\n\n.mobile-menu .dashboard-btn:hover {\n    background: linear-gradient(45deg, #e6b800, #cc5500);\n}\n\n/* ❌ Bouton de fermeture */\n.close-btn {\n    position: absolute;\n    top: 15px;\n    right: 20px;\n    font-size: 30px;\n    color: white;\n    background: none;\n    border: none;\n    cursor: pointer;\n    transition: transform 0.3s ease;\n}\n\n.close-btn:hover {\n    transform: rotate(90deg);\n}\n\n/* 🌍 RESPONSIVE */\n@media (max-width: 768px) {\n    .nav-box {\n        width: 100%;\n    }\n\n    .nav-links {\n        gap: 20px;\n    }\n\n    .mobile-menu {\n        width: 100%;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
